<script lang="ts">import { root, toReadableNodeState } from "../state/trackTree";
;
import { configStore } from "../state/settings";
import { request } from "../bridge/broker";
import colorLookup, { modalOptions } from "../colors";
import ImportModal from "../persistence/ImportModal.svelte";
import ExportModal from "../persistence/ExportModal.svelte";
import { getContext } from "svelte";
import { contextModalStore } from "./ContextModalStore";
import { audioStatusStore } from "../audio/audioPlayer";
;
import { create_in_transition } from "svelte/internal";
import { play } from "../audio/audioPlayer";
;
;
import toCss from "react-style-object-to-css";
export let parentStore;
export let branchStore;
export let depth;
export let offset;
export let parentOffset;
export let treeContainer;
let convertedParentStore;
$: convertedParentStore = toReadableNodeState(parentStore);
let parentState;
$: parentState = $convertedParentStore;
let branchState;
$: branchState = $branchStore;
let path;
$: path = branchState.path;
let childIndex;
$: childIndex = path[path.length - 1];
let pendingLoad;
$: pendingLoad = branchState.pendingLoad;
let section;
$: section = branchState.section;
let startsAt;
$: startsAt = section.startsAt;
let endsAt;
$: endsAt = section.endsAt;
let duration;
$: duration = endsAt - startsAt;
let childStores;
$: childStores = branchState.children;
function leftClick(event) {
    if (event.button === 0)
        root.select(path);
}
function rightClick({ clientX, clientY }) {
    contextModalStore.set({
        coordinates: [clientX, clientY],
        stores: {
            type: "branch",
            parentStore: parentStore,
            nodeStore: branchStore,
        },
    });
}
let onSelectedPath;
$: onSelectedPath = branchState.onSelectedPath;
let selectedByParent;
$: selectedByParent = branchState.selectedByParent;
let wasLastSelected;
$: wasLastSelected = branchState.wasLastSelectedByParent;
let trackPlaying = false;
let sectionPlaying = false;
let edgeProgress = 0;
let nodeColor;
$: nodeColor = onSelectedPath
    ? sectionPlaying
        ? colorLookup.nodePlaying
        : colorLookup.nodeActive
    : selectedByParent || (wasLastSelected && parentState.onSelectedPath)
        ? colorLookup.nodeWarm
        : colorLookup.nodeInactive;
let edgeColor;
$: edgeColor = onSelectedPath
    ? sectionPlaying
        ? colorLookup.edgePlaying
        : colorLookup.edgeActive
    : selectedByParent || (wasLastSelected && parentState.onSelectedPath)
        ? colorLookup.edgeWarm
        : colorLookup.edgeInactive;
let edgePercentage;
$: edgePercentage = sectionPlaying ? 100 : trackPlaying ? edgeProgress : 0;
let opacity;
$: opacity = onSelectedPath && trackPlaying ? 25 : 100;
let edgeZ;
$: edgeZ = onSelectedPath ? 1 : 0;
let numberOfLeavesStore;
$: numberOfLeavesStore = branchStore.numberOfLeavesStore;
let numberOfLeaves;
$: numberOfLeaves = $numberOfLeavesStore;
let placementOffset;
$: placementOffset = offset + -numberOfLeaves / 2;
let placementStore;
$: placementStore = branchStore.placementStore;
let childPlacements;
$: childPlacements = $placementStore;
let offsetWidth;
$: offsetWidth = Math.abs(parentOffset - offset);
let cw;
$: cw = offsetWidth * 30;
let ch;
$: ch = 150 / 2;
let lineWidth;
$: lineWidth = offsetWidth * 60 + 10;
let lineLeft;
$: lineLeft = Math.min(offset, parentOffset) * 60 - 5;
let node;
function focusNode() {
    if (node)
        node.focus();
}
function unfocusNode() {
    if ($contextModalStore === null)
        treeContainer.focus();
}
const { open } = getContext("simple-modal");
function loadMore() {
    request($configStore, branchStore, branchState);
}
function deleteBranch() {
    parentStore.deleteChildWithUndo(path[path.length - 1]);
}
function openImportModal() {
    open(ImportModal, {
        importUnderStore: branchStore,
    }, modalOptions);
}
function openExportModal() {
    open(ExportModal, {
        store: branchStore,
    }, modalOptions);
}
function keyPressed(event) {
    if (event.key === "r")
        return loadMore();
    if (event.key === "a")
        return openImportModal();
    if (event.key === "s")
        return openExportModal();
    if (event.key === "d")
        return deleteBranch();
}
function createNodeTransition(node, { offset }) {
    return {
        delay: Math.max(0, (endsAt - offset) * 1000),
        duration: 0,
        tick: (t) => {
            if (t === 0) {
                sectionPlaying = false;
            }
            else {
                sectionPlaying = true;
            }
        },
    };
}
function createEdgeTransition(node, { offset }) {
    const delay = Math.max(0, (startsAt - offset) * 1000);
    const duration = (endsAt - Math.max(startsAt, offset)) * 1000;
    const startProgressSeconds = Math.max(0, offset - startsAt);
    const startProgress = startProgressSeconds / (endsAt - startsAt);
    const progressToGo = 1 - startProgress;
    return {
        delay: delay,
        duration: duration,
        tick: (t) => {
            const progress = startProgress + progressToGo * t;
            edgeProgress = progress * 100;
        },
    };
}
let edgeGradient;
let nodeTransition;
let edgeTransition;
audioStatusStore.subscribe((status) => {
    if (node && edgeGradient) {
        if (nodeTransition)
            nodeTransition.end();
        if (edgeTransition)
            edgeTransition.end();
        trackPlaying = false;
        sectionPlaying = false;
        if (onSelectedPath && status.type === "on") {
            trackPlaying = true;
            edgeProgress = 0;
            nodeTransition = create_in_transition(node, createNodeTransition, status);
            nodeTransition.start();
            edgeTransition = create_in_transition(edgeGradient, createEdgeTransition, status);
            edgeTransition.start();
        }
    }
});
function clickedEdge(event) {
    console.log(event);
    if (!onSelectedPath)
        return;
    const clickX = event.offsetX;
    const clickY = event.offsetY;
    const width = event.target.viewportElement.clientWidth;
    const height = event.target.viewportElement.clientHeight;
    let progress;
    if (width === 10) {
        progress = clickY / height;
    }
    else {
        const fullDistance = Math.sqrt(Math.pow(width, 2) + Math.pow(height, 2));
        const clickDistance = Math.sqrt(Math.pow(clickX, 2) + Math.pow(clickY, 2));
        progress = clickDistance / fullDistance;
    }
    play(startsAt + progress * duration);
}
let pathStyle;
$: pathStyle = toCss({ cursor: onSelectedPath ? 'pointer' : 'initial' });
let lineStyle;
$: lineStyle = toCss({ left: lineLeft, top: (depth - 1) * ch * 2 + 24, transform: `scaleX(${offset < parentOffset ? -1 : 1})`, zIndex: edgeZ });
</script>

<style>
  .node {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 50px;
    height: 50px;
    border-radius: 50%;

    cursor: pointer;
    outline: none;
    transition: transform 0.2s ease-in-out, background-color 0.2s ease-in-out,
      opacity 0.2s ease-in-out;
  }

  .node:hover {
    transform: scale(1.1, 1.1);
    transform-origin: center;
  }

  .label {
    font-size: 30px;
  }

  .pendingLoad {
    font-size: 18px;
    text-align: center;
    margin: 8px 0 0 0;
    border-radius: 30%;
    width: 100%;
  }

  .line {
    position: absolute;
  }

  .placement {
    position: absolute;
    z-index: 2;
  }

  path {
    transition: stroke 0.2s ease-in-out;
  }
</style>

<div
  class="placement"
  style={toCss({top: 150 * depth, left: 60 * offset - 25, pointerEvents: opacity === 100 ? "initial": "none"})}>
  <div
    on:mousedown={leftClick}
    on:contextmenu|preventDefault={rightClick}
    on:mouseenter={focusNode}
    on:mouseleave={unfocusNode}
    on:keypress={keyPressed}
    bind:this={node}
    class="node"
    style={toCss({backgroundColor: nodeColor, opacity: opacity})}
    tabindex={0}>
    <span class="label">{childIndex}</span>
  </div>
  {#if pendingLoad > 0}
    <p
      class="pendingLoad"
      style={toCss({color: colorLookup.textDark, backgroundColor: colorLookup.bgLight, border: "2px solid", borderColor: colorLookup.border})}>
      +{pendingLoad}
    </p>
  {/if}
</div>
{#if childPlacements.length > 0}
  {#each childPlacements as [idx, placement] (idx)}
    <svelte:self
      parentStore={branchStore}
      branchStore={childStores[idx]}
      depth={depth + 1}
      offset={placementOffset + placement}
      parentOffset={offset}
      {treeContainer} />
  {/each}
{/if}
<svg
  class="line"
  width={lineWidth}
  height={ch * 2 + 2}
  style={lineStyle}>
  <linearGradient
    bind:this={edgeGradient}
    id={`linear${depth},${offset}`}
    gradientUnits="userSpaceOnUse"
    x1="0%"
    y1="0%"
    x2={offset === parentOffset ? '0%' : '100%'}
    y2="100%">
    <stop offset="0%" stop-color={colorLookup.edgePlaying} />
    <stop offset={edgePercentage + '%'} stop-color={colorLookup.edgePlaying} />
    <stop offset={edgePercentage + '%'} stop-color={edgeColor} />
  </linearGradient>
  <path
    d={`m 5 0 c 0 ${ch + 0.5} ${cw * 2} ${ch + 0.5} ${cw * 2} ${ch * 2 + 1}`}
    stroke={`url(#linear${depth},${offset})`}
    stroke-width="6px"
    fill="none"
    style={pathStyle}
    on:click={clickedEdge} />
</svg>
